import React, { StrictMode, useState, lazy, Suspense } from "react";
// import { render } from "react-dom";
import { Link, Route, Routes } from "react-router-dom";
// import Pet from "./Pet";
// import SearchParams from "./SearchParams";
// import Details from "./Details";
import ThemeContext from "./ThemeContext";

// dynamic import of components using lazy
const Details = lazy(() => import("./Details"));
const SearchParams = lazy(() => import("./SearchParams"));

const App = () => {
  // creating a theme state
  const theme = useState("darkblue");
  return (
    // Wrap child components in the Context Provider and supply the state value.
    // All components in the tree will have the access to theme context
    <StrictMode>
      <ThemeContext.Provider value={theme}>
        <div
          className="p-0 m-0"
          style={{
            background:
              "url(http://pets-images.dev-apis.com/pets/wallpaperB.jpg)",
          }}
        >
          <Suspense
            fallback={
              <div className="absolute h-screen w-screen flex justify-center items-center bg-gradient-to-tr from-cyan-500 to-blue-500 text-white">
                loading
              </div>
            }
          >
            <header className="w-full mb-10 text-center p-7 bg-gradient-to-b from-purple-500 via-pink-500 to-red-500">
              <Link to="/" className="text-6xl text-white hover:text-gray-200">
                <h1 id="adopt-me-logo">Adopt Me!</h1>
              </Link>
            </header>
            <Routes>
              <Route path="/" element={<SearchParams />} />
              <Route path="/details/:id" element={<Details />} />
            </Routes>
          </Suspense>
          ;
        </div>
      </ThemeContext.Provider>
    </StrictMode>
  );
};

export default App;

